@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  font-family: upheaval;
  src: url(/memory-card/static/media/upheavtt.e3d365ba.ttf);
}

html {
  background-color: pink;
}

body {
  overflow: hidden;
}

.author {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header {
  font-family: upheaval;
  height: 4rem;
  align-items: center;
  font-size: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: rgb(255 0 0);
  padding: 0.8rem;
}

.header div p {
  margin-bottom: 0px;
}

.author {
  font-size: 1rem;
}

.images-card {
  /* height: 100px;
  width: 100px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.images-card img {
  cursor: pointer;
}

.images-card img:hover {
  opacity: 0.8;
}

.card {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  max-width: 70%;
  max-height: 70%;
  margin: auto;
  margin-top: -1rem;
}

.images-card span {
  display: flex;
  position: relative;
  top: -2rem;
  width: 150px;
  justify-content: center;
}

.message {
  font-family: upheaval;
  font-size: 1.5rem;
  background-color: rgba(255, 0, 0, 0.848);
  display: flex;
  justify-content: center;
  padding: 0.2rem;
  height: 2rem;
}

.message p {
  margin: 0;
}

.score {
  font-size: 1.5rem;
  display: flex;
}

@media only screen and (max-width: 600px) {
  .card {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .header {
    font-size: 1.5rem;
  }
  .header div {
    width: 150px;
  }

  .message {
    font-size: 1.1rem;
  }

  .score {
    font-size: 1.1rem;
    display: flex;
  }
}

@media only screen and (max-width: 400px) {
  .card {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .header {
    font-size: 1rem;
  }
  .header div {
    width: 100px;
  }

  .message {
    font-size: 1rem;
  }

  .score {
    font-size: 1rem;
    display: flex;
  }
}

